<template>
    <div class="zpf-box">
        <div class="model" @click="close"> </div>
        <div class="zpf-dialog">
            <i class="zpf-close" @click="close">
                <s class="zpf-close-line"></s>
            </i>
            <div class="zpf-header">
                Zone申请
            </div>
            <form class="zpf-form">
                <div class="zpf-place">
                    <label for="place">
                        Place
                    </label>
                    <select v-model="location" name="place" id="">
                        <option v-for="item in places" :value="item.value">
                            {{item.text}}
                        </option>
                    </select>
                </div>
                <div class="zpf-date">
                    <label for="date">
                        Start Date
                    </label>
                    <date-picker class="zpf-date-input" :date="startTime" :option="option" :limit="limit"></date-picker>
                </div>
                <div class="zpf-period">
                    <label for="period">
                        Period
                    </label>
                    <select id="period" v-model="period">
						<option disabled="true">Period</option>
						<option v-for="item in periods" :value="item.value">{{item.text}}</option>
					</select>
                </div>
                <div v-show="show_model" class="zpf-type">
                    <label for="type">
                        Model
                    </label>
                    <select id="type" v-model="model">
						<option disabled="true">Model</option>
						<option v-for="item in model" :value="item.value">{{item.text}}</option>
					</select>
                </div>
                <div class="zpf-name">
                    <label for="name">
                        FirstName
                    </label>
                    <input id="name" v-model="first_name" type="text" placeholder="Example: John">
                </div>
                <div class="zpf-phone">
                    <label for="phone">
                        Phone
                    </label>
                    <input id="phone" v-model="phone" type="text" placeholder="Example: 18988889999">
                </div>
                <div class="zpf-email">
                    <label for="email">
                        E-mail
                    </label>
                    <input id="email" v-model="email" type="text" placeholder="Example: alpha@gmail.com">
                </div>
                <div class="zpf-address">
                    <label for="address">
                        Company
                    </label>
                    <input id='address' v-model="addr" type="text" placeholder="Example: Alpha co">
                </div>
                <div class="zpf-position">
                    <label for="position">
                        交易平台
                    </label>
                    <input id='position' type="text" placeholder="若无则可不填">
                </div>
                
                <div class="zpf-cost">
                    <label for="cost">
                        Fee
                    </label>
                    <span>
                        ￡{{$store.state.pay_info.price*period}}
                    </span>
                </div>
                <div class="zpf-btns">
                    <button @click="openPay" class="zpf-submit btn-primary" type="button">
                        Next
                    </button>
                </div>
            </form>
        </div>
        
    </div>
</template>

<script>
    import Datepicker from 'vue-datepicker'
    export default {
        data() {
            return {
                location: 1,
                price: 0.00,
                first_name: '',
                phone: '',
                period: '',
                email: '',
                addr: '',
                model: '',
                show_model: false,
                places: [
                    { text: 'Lodon', value: 1 },
                    { text: 'Shanghai', value: 2 },
                    { text: 'Manchester', value: 3 },
                    { text: 'Chengdu', value: 4 }
                ],
                periods: [
					{ value: '1', text: 'one month' },
					{ value: '2', text: 'two month' },
					{ value: '3', text: 'three month' },
					{ value: '4', text: 'four month' },
					{ value: '5', text: 'five month' },
					{ value: '6', text: 'six month' }
				],
                models: [
					{ value: '1', text: '1' },
					{ value: '2', text: '2' },
					{ value: '3', text: '3' }
				],
                // for Vue 2.0 
				startTime: {
					time: ''
				},
				endtime: {
					time: ''
				},
				option: {
					type: 'day',
					week: ['Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa', 'Su'],
					month: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
					format: 'YYYY-MM-DD',
					placeholder: 'Start Date',
					inputStyle: {
    					'display': 'inline-block',
                        'padding': '0px 10px',
                        'height': '40px',
                        'width': '320px',
                        'box-sizing': 'border-box',
                        'border-radius': '2px',
                        'line-height': '40px',
                        'font-size': '14px',
                        'background-color': '#fff',
                        'border':' 1px solid #e5e6ea',
                        'margin-right': '20px',
                        'overflow': 'hidden'
					},
					color: {
    					header: '#666',
    					headerText: '#fff'
					},
					buttons: {
    					ok: 'Ok',
    					cancel: 'Cancel'
					},
					overlayOpacity: 0.5, // 0.5 as default 
					dismissible: true // as true as default 
				},
				timeoption: {
					type: 'min',
					week: ['Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa', 'Su'],
					month: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
					format: 'YYYY-MM-DD HH:mm'
				},
				multiOption: {
					type: 'multi-day',
					week: ['Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa', 'Su'],
					month: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
					format:"YYYY-MM-DD HH:mm"
				},
				limit: [{
					type: 'weekday',
					available: [1, 2, 3, 4, 5]
				},
				{
					type: 'fromto',
					from: '2016-02-01',
					to: '2016-02-20'
				}]
            }
        },
        mounted() {
            const self = this
            self.location = self.$store.state.zone_forms.location
            self.$store.state.zone_type == 1 && ( show_model = true )
            let formData = new FormData()
			formData.append('token',sessionStorage.getItem('token'))
            fetch(self.$store.state.api_addr + 'user/user_info_center',{
                method: 'post',
                mode: 'cors',
                body: formData
            }).then((res) => {
                res.ok && res.json().then((json) => {
                    self.email = json.data.email
                    self.phone = json.data.phone
                    self.first_name = json.data.first_name
                    self.position = json.data.pro
                    self.addr = json.data.organization
                })
            })
        },
        methods: {
            close() {
				let self = this
				self.$store.dispatch('TOGGLEZPFORM','off')
			},
            openPay() {
                let self = this
                let formData = new FormData()
                self.$store.dispatch('CHANGEZPPAY',self.$store.state.pay_info.price*self.period)
                self.$store.dispatch('TOGGLEACTPAY','on')
                self.$store.dispatch('TOGGLEZPFORM','off')
                formData.append('token',sessionStorage.getItem('token'))
                formData.append('first_name',self.first_name)
			    formData.append('phone',self.phone)
			    formData.append('pro',self.position)
			    formData.append('organization',self.addr)
                self.$store.dispatch('CHANGEPAYTABLE','tr_fl_order')
                fetch(self.$store.state.api_addr + 'user/update_user_info',{
                    method: 'post',
                    mode: 'cors',
                    body: formData
                }).then((res) => {
                    res.ok && res.json().then((json) => {
                        switch(json.archive.status){
                            case 0:
                                let formData = new FormData()
                                formData.append('location',self.location)
                                formData.append('start',self.startTime)
                                formData.append('limit',self.period)
                                fetch(self.$store.state.api_addr + 'ZoneAndPlus/made_zone_info',{
                                    method: 'post',
                                    mode: 'cors',
                                    body: formData
                                }).then((res) => {
                                    self.$store.dispatch('TOGGLEACTPAY','on')
                                    self.$store.dispatch('TOGGLEZPFORM','off')
                                })
                                break;
                        }
                    })
                })
            }
        },
        components: {
            'date-picker': Datepicker
        }
    }
</script>

<style scoped lang="scss">
    @import '../css/alpha.scss';

    $zpfheight: 36px;

    .zpf-box{
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgba(1,1,1,.5);
        z-index: 999;
        overflow: scroll;
        .model{
            position: absolute;
            width: 100%;
            height: 100%;
            z-index:100;
        }
        .zpf-dialog{
            position: relative;
            top:expression(eval(document.documentElement.scrollTop));
            margin:120px auto;
            width: 600px;
            background: #fff;
            box-shadow: 0 0 18px rgba(0,0,0,.01);
            border-radius: 8px;
            z-index: 2000;
            .zpf-close{
				position: absolute;
				right: 0;
				top: -50px;
				width: 30px;
				height: 30px;
				background-image: url(../assets/images/close.png);
				background-position: center center;
				background-repeat: no-repeat;
				background-size: 100% 100%;
				cursor: pointer;
				.zpf-close-line{
					width: 2px;
					height: 20px;
					position: absolute;
					bottom: -20px;
					left: 50%;
					margin-left: -1px;
					background-color: #fff;
				}
				&:hover{
					color: $gray3;
				}
			}
            .zpf-header{
                width: 100%;
                overflow: hidden;
                font-size: 18px;
                font-weight: bold;
                text-align: center;
                line-height: 100px;
                color:$gray1;
                .zpf-header-icon{
                    display: block;
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    background-image: url(../assets/images/act_form_icon.png);
                    background-size: 40px;
                    background-position: center center;
                    background-repeat: no-repeat;
                }
            }
            .zpf-form{
                padding:0 70px 30px;
                .zpf-place,.zpf-date,.zpf-period,.zpf-type,.zpf-name,.zpf-phone,.zpf-email,.zpf-address,.zpf-position,.zpf-combo,.zpf-cost{
                    padding-bottom: 20px;
                    overflow: hidden;
                    label{
                        width: 20%;
                        color: $gray1;
                        font-size: 14px;
                        font-weight: 600;
                        box-sizing: border-box;
                        padding-right: 10px;
                        line-height: $zpfheight;
                        text-align: right;
                        float: left;
                        overflow: hidden;
                    }
                    input{
                        width: 70%;
                        border: 1px solid $gray5;
                        border-radius: 3px;
                        padding-left: 10px;
                        box-sizing: border-box;
                        line-height: $zpfheight;
                        outline: none;
                        float: left;
                    }
                    select{
                        width: 70%;
                        height: 40px;
                        border: 1px solid $gray5;
                        border-radius: 3px;
                        padding-left: 10px;
                        box-sizing: border-box;
                        line-height: $zpfheight;
                        outline: none;
                        float: left;
                    }
                    span{
                        float: left;
                        line-height: $zpfheight;
                        width: 50%;
                        color: $danger;
                        font-weight: 900;
                    }
                    .zpf-date-input{
                        width: 70%;
                        .cov-datepicker{
                            width: 100% !important;
                            box-sizing: border-box;
                            padding-left: 10px;
                        }
                    }
                }
                .zpf-cost{
                    padding: 0;
                }
                .zpf-btns{
                    width: 100%;
                    margin:30px 0;
                    .zpf-submit{
                        margin: 0 auto;
                        display: block;
                        position: relative;
                    }
                }
                
            }
        }
    }
</style>


